import { createRouter, createWebHistory } from "vue-router";
import LogIn from "./views/LogIn.vue";
import SignUp from "./views/SignUp.vue";
import HomeView from "./views/Home.vue";
import App from "./App.vue";

const routes = [
	{
		path: "/mtic_ciclo3_front/",
		name: "root",
		component: App,
	},
	{
		path: "/mtic_ciclo3_front/home",
		name: "home",
		component: HomeView,
	},
	{
		path: "/mtic_ciclo3_front/auth/login",
		name: "login",
		component: LogIn,
	},
	{
		path: "/mtic_ciclo3_front/auth/signup",
		name: "signup",
		component: SignUp,
	},
	{
		path: "/mtic_ciclo3_front/doctor",
		name: "doctor",
		component: () => import(/* webpackChunkName: "doctor" */ "./views/DoctorView.vue"),
	},
	{
		path: "/mtic_ciclo3_front/patient",
		name: "patient",
		component: () => import(/* webpackChunkName: "doctor" */ "./views/PatientView.vue"),
	},
	{
		path: "/mtic_ciclo3_front/account",
		name: "account",
		component: () =>
			import(/* webpackChunkName: "account" */ "./views/Account.vue"), // other form of importing
	},
	{
		path: "/mtic_ciclo3_front/assistant/",
		name: "assistant",
		component: () =>
			import(
				/* webpackChunkName: "assistant" */ "./views/RegisterWithRoles.vue"
			),
	},
	{
		path: "/mtic_ciclo3_front/assistant/:id",
		name: "assistant_edit_user",
		component: () =>
			import(
				/* webpackChunkName: "assistant" */ "./views/AssistantEditPatient.vue"
			),
	},
];

const router = createRouter({
	history: createWebHistory(),
	routes,
});

export default router;
